html {
  height: 100vh;
}

.no-select {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently */
}

.App {
  text-align: center;
  font-size: 62.5%;
  background: linear-gradient(to top left, rgb(1, 53, 88), rgb(206, 237, 241));
  height: 100vh;
}

.title {
  font-size: 7.6rem;
  background: linear-gradient(rgb(201, 37, 138), rgb(37, 42, 121));
  background: -webkit-linear-gradient(rgb(201, 37, 138), rgb(37, 42, 121));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: "Dancing Script", cursive;
  font-family: "Monoton", cursive;
  color: #8695c7;
}

.gear-icon {
  position: fixed;
  right: 1rem;
  top: 1rem;
  height: 3.5rem;
  color: white;
  animation: rotate 18s infinite linear;
  cursor: pointer;
  z-index: 1000;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.settings-menu {
  position: fixed;
  right: 0.5rem;
  top: 5rem;
  width: 10rem;
  background: linear-gradient(to top right, #bbb, #eee);
  border-radius: 0.3rem;
  padding: 0.4rem;
  box-shadow: 0px 0px 1.6rem rgba(0, 0, 0, 0.3);
  z-index: 1000;
}

.background-modal {
  height: 100vh;
  width: 100vw;
  position: fixed;
  background: #333;
  opacity: 0.8;
  z-index: 750;
}

.word-length-wrapper {
  font-size: 1rem;
  font-weight: bold;
}

.word-length {
  margin-top: -1rem;
}

.change-word-length-btn {
  background: linear-gradient(rgb(52, 117, 202), rgb(26, 73, 161));
  background: -webkit-linear-gradient(rgb(51, 105, 187), rgb(27, 89, 170));
  color: white;
  height: 2.2rem;
  width: 2.2rem;
  margin: 0.2rem;
  border: 1px solid rgb(74, 143, 248);
  border-radius: 50%;
  font-size: 1.8rem;
  cursor: pointer;
}

.change-word-length-btn:active {
  font-weight: bold;
  color: #fff;
}

.word-length {
  background: linear-gradient(rgb(201, 37, 138), rgb(37, 42, 121));
  background: -webkit-linear-gradient(rgb(201, 37, 138), rgb(37, 42, 121));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 4rem;
}

.settings-msg {
  font-size: 0.8rem;
  font-weight: bold;
  margin: 0.8rem;
}

.attempt-line {
  margin: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  transition: all 2s ease;
}

.letter-square {
  height: 7rem;
  width: 7rem;
  border: 2.2px solid rgb(50, 102, 161);
  margin: 1rem;
  border-radius: 0.7rem;
  background: linear-gradient(to top right, #ccc, #eee);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 4.2rem;
  color: rgb(61, 65, 124);
  transition: all 2s ease;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);
}

.shake {
  animation: shake 0.8s ease-in-out;
}

@keyframes shake {
  0% {
    transform: skewX(-3deg);
  }

  10% {
    transform: skewX(3deg);
  }
  20% {
    transform: skewX(-3deg);
  }

  30% {
    transform: skewX(3deg);
  }
  40% {
    transform: skewX(-3deg);
  }

  50% {
    transform: skewX(3deg);
  }
  60% {
    transform: skewX(-3deg);
  }

  70% {
    transform: skewX(3deg);
  }
  80% {
    transform: skewX(-3deg);
  }
  90% {
    transform: skewX(3deg);
  }
  100% {
    transform: skewX(0deg);
  }
}

.input {
  margin: 0;
  width: 100%;
  position: fixed;
  bottom: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.kb-row {
  display: flex;
  align-items: center;
  flex-direction: row;
  height: 6rem;
}

.key {
  box-sizing: border-box;
  padding: 1rem;
  height: 4rem;
  min-width: 4rem;
  font-size: 1.8rem;
  margin: 0 0.3rem 0 0.3rem;
  background: linear-gradient(to top right, #ccc, #eee);
  border-radius: 0.3rem;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: none;
}

.key:active {
  background: linear-gradient(
    to top right,
    rgb(209, 209, 195),
    rgb(223, 223, 202)
  );
}

*:disabled {
  cursor: default;
}

.key-text {
  pointer-events: none;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently */
}

.DEL {
  background: linear-gradient(
    to bottom right,
    rgb(122, 28, 28),
    rgb(99, 52, 52)
  );
  color: white;
}

.ENTER {
  background: linear-gradient(
    to bottom right,
    rgb(77, 109, 78),
    rgb(65, 82, 65)
  );
  color: white;
}

.answer-submit-btn {
  height: 4.5rem;
  width: 12rem;
  border-radius: 0.5rem;
  border: none;
  margin-top: 1.5rem;
  font-size: 1.5rem;
  background: linear-gradient(to top left, rgb(83, 9, 67), rgb(22, 10, 88));
  color: white;
  cursor: pointer;
}

.shrink {
  height: 4rem;
}

.shrink > div {
  height: 3rem;
  width: 3rem;
  font-size: 2rem;
  margin: 0.4rem;
}

.correct {
  color: white;
  background: linear-gradient(to bottom right, rgb(23, 85, 11), rgb(14, 80, 5));
}

.present {
  color: white;
  background: linear-gradient(
    to bottom right,
    rgb(194, 166, 10),
    rgb(163, 127, 7)
  );
}

.complete-modal {
  position: fixed;
  box-sizing: border-box;
  left: 50%;
  top: 50%;
  padding: 2rem;
  transform: translate(-50%, -50%);
  height: 14rem;
  width: 60rem;
  color: white;
  font-size: 2rem;
  background: linear-gradient(to top right, rgb(27, 45, 97), rgb(2, 97, 104));
  border-radius: 1rem;
  z-index: 100;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.2);
}

.complete-btn {
  height: 4rem;
  width: 16rem;
  font-size: 1.8rem;
  border-radius: 0.5rem;
  background: linear-gradient(to top right, rgb(158, 118, 9), rgb(207, 188, 9));
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.3);
  outline: none;
  border: none;
  cursor: pointer;
}

.invalid {
  background: linear-gradient(to top right, #333, #555);
  color: white;
  font-weight: bold;
  border-radius: 0.5rem;
  font-size: 0.9rem;
  height: 3rem;
  width: 9.5rem;
  opacity: 0;
  animation: fade 1.5s ease-in-out;
  position: fixed;
  justify-content: center;
  align-items: center;
  left: 50%;
  transform: translateX(-50%);
  z-index: 500;
}

@keyframes fade {
  0% {
    opacity: 0;
  }

  25% {
    opacity: 50;
  }

  50% {
    opacity: 100;
  }

  75% {
    opacity: 50;
  }

  100% {
    opacity: 0;
  }
}

*:disabled {
  background: linear-gradient(to top right, #333, #555);
  opacity: 0.5;
  color: white;
}

*:disabled:hover {
  background: linear-gradient(to top right, #333, #555);
}

@media screen and (max-width: 820px) {
  .letter-square {
    height: 5rem;
    width: 5rem;
    font-size: 3.1rem;
  }

  .kb-row {
    height: 3.3rem;
  }

  .key {
    margin: 0 0.2rem 0 0.2rem;
    height: 2.4rem;
    max-width: 7rem;
    min-width: 3rem;
    font-size: 1.3rem;
    padding: 0.4rem;
  }

  .key-text {
    margin: 0rem;
  }
}

@media screen and (max-width: 650px) {
  .title {
    font-size: 5.8rem;
    margin-bottom: 0;
  }

  .gear-icon {
    height: 2.5rem;
  }

  .settings-menu {
    top: 4rem;
  }

  .letter-square {
    margin: 0.4rem;
    margin-top: 1.8rem;
  }

  .key {
    height: 2.4rem;
    max-width: 6rem;
    min-width: 2.4rem;
    font-size: 1.1rem;
    padding: 0.3rem;
  }

  .complete-modal {
    font-size: 1.6rem;
    padding: 2rem;
    width: 100%;
  }
}

@media screen and (max-width: 500px) {
  .title {
    font-size: 3.7rem;
  }

  .shrink > div {
    font-size: 1.4rem;
    font-weight: bold;
    height: 2.6rem;
    width: 2.6rem;
    margin: 0.3rem;
  }

  .letter-square {
    height: 4rem;
    width: 4rem;
    font-size: 2.4rem;
  }

  .key {
    height: 2.4rem;
    max-width: 5rem;
    min-width: 1.8rem;
    font-size: 1.1rem;
    padding: 0.3rem;
  }

  .complete-modal {
    width: 80vw;
    height: 55vw;
    font-size: 1.5rem;
  }

  .complete-btn {
    height: 2.5rem;
    width: 6rem;
    font-size: 1rem;
    font-weight: bold;
  }
}

@media screen and (max-width: 400px) {
  .title {
    font-size: 3.2rem;
    padding-top: 1.3rem;
  }
  .shrink {
    height: calc(100vw * 0.15);
  }

  .shrink > div {
    height: calc(100vw * 0.11);
    width: calc(100vw * 0.11);
    font-size: calc(100vw * 0.06);
  }

  .letter-square {
    height: calc(100vw * 0.15);
    width: calc(100vw * 0.15);
    margin-left: calc(100vw * 0.01);
    margin-right: calc(100vw * 0.01);
    font-size: calc(100vw * 0.08);
    font-weight: bold;
  }

  .key {
    height: 2.4rem;
    max-width: 3rem;
    min-width: 1.5rem;
    font-size: 0.8rem;
    padding: 0.3rem;
  }

  .complete-modal {
    width: 80vw;
    height: 55vw;
    font-size: 1rem;
  }

  .complete-btn {
    height: 2.5rem;
    width: 6rem;
    font-size: 1rem;
    font-weight: bold;
  }
}

@media screen and (max-width: 340px) {
  .title {
    font-size: 2.6rem;
  }

  .kb-row {
    height: 2.9rem;
  }

  .key {
    height: 2.4rem;
    max-width: 3rem;
    min-width: 1.35rem;
    font-size: 0.8rem;
    padding: 0.3rem;
  }

  .complete-modal {
    font-size: 1rem;
  }

  .complete-btn {
    height: 2.5rem;
    width: 6rem;
    font-size: 1rem;
    font-weight: bold;
  }
}

@media screen and (max-width: 310px) {
  .kb-row {
    height: 2.9rem;
  }

  .key {
    height: 2.2rem;
    max-width: 3rem;
    min-width: 1.2rem;
    font-size: 0.8rem;
    padding: 0.2rem;
  }

  .complete-modal {
    width: 80vw;
    height: 70vw;
    font-size: 1rem;
  }

  .complete-btn {
    height: 2.5rem;
    width: 6rem;
    font-size: 1rem;
    font-weight: bold;
  }
}
